@import "https://fonts.googleapis.com/css2?family=Ribeye&display=swap";
@import "https://fonts.googleapis.com/css2?family=Roboto&display=swap";
@import "https://fonts.googleapis.com/css2?family=Gelasio&display=swap";
html {
  width: 100%;
}

body {
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
  display: flex;
}

.banner {
  width: 100%;
  min-height: 900px;
  box-sizing: border-box;
  background-color: #0000;
  background-image: linear-gradient(#0000004d, #0000004d 100%), url("Rectangle.056b34f2.png");
  background-position: 0 0, 0 0;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: scroll, scroll;
  background-origin: padding-box, padding-box;
  background-clip: border-box, border-box;
  flex-direction: column;
  display: flex;
}

.banner__header {
  max-width: 1600px;
  height: 100px;
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
  padding: 0 230px;
  display: flex;
}

.banner__header__logo {
  letter-spacing: .06em;
  color: #fff;
  cursor: pointer;
  text-shadow: 1px 0 10px gold;
  font-family: Ribeye;
  font-size: 24px;
  font-style: normal;
  font-weight: 400;
  transition: all 1s;
}

.banner__header__logo:hover {
  color: gold;
  transform: scale(2);
}

.banner__header__nav {
  text-align: center;
  align-items: center;
  display: flex;
}

.banner__header__nav__name {
  color: #fff;
  margin-right: 50px;
  font-family: Roboto;
  font-size: 18px;
  font-style: normal;
  font-weight: 500;
  text-decoration: none;
  transition: all .5s;
}

.banner__header__nav__name:hover {
  color: gold;
  transform: translateY(-5px);
}

.banner__header__button {
  width: 163px;
  height: 51px;
  color: #fff;
  box-sizing: border-box;
  cursor: pointer;
  background: #c9453b;
  border: none;
  border-radius: 5px;
  font-family: Roboto;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  transition: all .5s;
}

.banner__header__button:hover {
  color: #c9453b;
  background: #adff2f;
}

.banner__header__menu {
  display: none;
}

.banner__content {
  max-width: 1600px;
  flex-direction: column;
  padding: 186px 0 244px 218px;
  display: flex;
}

.banner__content__text1 {
  color: #fff;
  text-shadow: 1px 0 10px #fff;
  font-family: Gelasio;
  font-size: 65px;
  font-style: normal;
  font-weight: 700;
  line-height: 78px;
}

.banner__content__text2 {
  color: #fff;
  opacity: .6;
  margin-top: 30px;
  font-family: Roboto;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 29px;
}

.banner__content__button {
  width: 187px;
  height: 59px;
  color: #fff;
  cursor: pointer;
  box-sizing: border-box;
  background: #c9453b;
  border: none;
  border-radius: 5px;
  margin-top: 40px;
  font-family: Roboto;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
  transition: all .5s;
}

.banner__content__button:hover {
  color: #c9453b;
  background: #adff2f;
}

